import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import Navbar from "./navbar"

const Header = ({ siteTitle }) => (
	<header style={{ background: `black`, marginBottom: `1.45rem` }}>
		<div style={{ margin: `0 auto`, maxWidth: 1260, padding: `1.45rem 1.0875rem`, display: 'flex' }}>
			<Navbar />
			<div style={{ margin: 0, fontSize: '21pt' }}>
				<Link to="/" style={{color: `white`, textDecoration: `none` }}>
					{/*siteTitle*/}
					<span style={{
						textTransform: 'uppercase',
						letterSpacing: '1px'
					}}>Bryce Egley</span>
					<span style={{fontSize: '11pt', marginLeft: 10}}>(.com)</span>
				</Link>
			</div>
		</div>
	</header>
)

Header.propTypes = {
	siteTitle: PropTypes.string,
}

Header.defaultProps = {
	siteTitle: ``,
}

export default Header
