/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"

import Header from "./header"
import "./layout.css"
import GatsbyIcon from "./gatsbyicon"


const Layout = ({ children }) => {
	const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

	return (
		<>
			<Header siteTitle={data.site.siteMetadata?.title || `Title`} />
			<div style={{ margin: `0 auto`, maxWidth: 1260, padding: `0 1.0875rem 1.45rem` }}>
				<main>{children}</main>
				<footer style={{marginTop: `4rem`, display: 'flex' }}>
					<div style={{ width: `25px`, marginRight: 10}}>
						<GatsbyIcon />
					</div>
					<div style={{fontSize: '12pt'}}>
						Powered by {` `}<a href="https://www.gatsbyjs.com">Gatsby</a>
					</div>
				</footer>
			</div>
		</>
	)
}

Layout.propTypes = {
	children: PropTypes.node.isRequired,
}

export default Layout
