import { Drawer, List, ListSubheader } from '@material-ui/core';
import { Menu } from '@material-ui/icons';
import React, { useState } from 'react'
import Navlink from './navlink';

const Navbar = () => {
    const [renderMenu, toggleRender] = useState(false);


    return(
        <div style={{marginRight: 15}}>
            <Menu onClick={() => toggleRender(true)} style={{color: 'darkturquoise', cursor: 'pointer'}} />
            <Drawer anchor="left" open={renderMenu} onClose={() => toggleRender(false)}>
                <div style={{padding: `1.45rem 1.0875rem`, backgroundColor: 'rgb(224, 224, 224)', height: '100%'}}>
                    <List component="nav" aria-labelledby="nested-list-subheader"
                        subheader={
                            <ListSubheader component="div" id="nested-list-subheader">
                                <span>What's up?</span>
                            </ListSubheader>
                        }
                    >
                        <Navlink name="Home" path="/" />
                        <Navlink name="Blurbs" path="/blurbs/" />
                        <Navlink name="Projects" path="/projects/"/>
                        <Navlink name="About" path="/about/"/>
                    </List>
                </div>
            </Drawer>
        </div>
    )
}

export default Navbar;